/* Body styles */
body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
  background-color: #f4f4f4;
  background-image: linear-gradient(to bottom right, #4ddfdf, #2195f38a);
  color: #333;
}

/* Header styles */
header {
  background-color: #fff;
  background-image: url("./images/background-image.jpg");
  padding: 20px;
  text-align: center;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  top: 0;
  width: 100%;
}

@media only screen and (max-width: 600px) {
  header {
    background-size: contain;
    background-position: top;
  }
}

.header-text {
  color: white;
}

header h1 {
  margin: 10px 0;
  color: #fdfcfc;
  font-family: 'Montserrat', sans-serif;
  font-weight: 700;
}

h2 {
  color: #666666;
}

h3 {
  color: #999999;
}

p {
  color: #444444;
}

.paragraph-container p {
  line-height: 1.6;
  margin-bottom: 15px;
  font-size: 16px;
  color: #333;
  font-family: 'Roboto', sans-serif;

}

li {
  color: #555555;
}

/* Navbar container */
.navbar {
  width: 100%;
  position: fixed;
  background-color: #333;
  overflow: hidden;
  background: linear-gradient(to right, #2196f3, #7825d8);
}

/* Links inside the navbar */
.navbar a {
  float: left;
  display: block;
  color: #fff;
  text-align: center;
  padding: 14px 20px;
  text-decoration: none;
}

.navbar a:hover {
  background-color: #ddd;
  color: #333;
}

.navbar a.active {
  background-color: #666;
}

/* Main content sections */
section {
  padding: 20px;
  background-color: rgba(255, 255, 255, 0.9);
  margin: 20px;
  border-radius: 5px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  transition: background-color 0.3s ease;
}

.section:hover {
  background-color: rgba(255, 255, 255, 1);
}

/* Profile picture */
.profile-img {
  margin-top: 50px;
  width: 150px;
  height: auto;
  margin-bottom: 20px;
  border-radius: 50%;
  border: 4px solid #fff;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}

#skills {
  margin-top: 20px;
}

#skills h2 {
  font-size: 24px;
  margin-bottom: 10px;
}

#skills p {
  margin-bottom: 20px;
}

#skills ul {
  list-style-type: none;
  padding-left: 0;
}

#skills ul li {
  margin-bottom: 10px;
  font-size: 16px;
}

#skills ul li i {
  margin-right: 10px;
}

.project-image {
  max-width: 100%;
  height: auto;
  display: block;
  margin: 0 auto;

}

.project-link {
  color: #007bff;
  text-decoration: none;
  display: inline-block;
  margin-bottom: 10px;
}

/* Style the contact section */
.contact-ul {
  list-style-type: none;
  padding: 0;
}

.contact-a {
  text-decoration: none;
  color: #333;
  font-size: 16px;
}

.contact-icon {
  margin-right: 10px;
}

.contact-li {
  margin-bottom: 10px;
}

/* Style the map container */

.map-container {
  margin-bottom: 20px;
  margin-left: 20px;
}

footer {
  background-color: #333;
  color: #db0606;
  padding: 20px;
  display: flex;
  justify-content: space-between;
}

.address {
  flex: 1;
  margin-right: 20px;
}

.map {
  flex: 1;
}

.map iframe {
  width: 100%;
  height: 300px;
}

.address-style {
  color: #fdfcfc;
}

.fa-map-marker-alt {
  margin-right: 5px;
}

.return-button {
  display: inline-block;
  margin: 10px;
  padding: 10px 20px;
  background-color: #f1461b;
  color: white;
  text-decoration: none;
  border-radius: 5px;
  transition: background-color 0.3s ease;
}

#contact .contact-a:hover {
  color: #0056b3;
  cursor: pointer;
}

.paragraph-container {
  max-width: 1000px;
}

.section-container {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
}

.column {
  flex: 1;
  min-width: 300px;
  max-width: calc(50% - 20px);
}

@media screen and (max-width: 768px) {
  .column {
    max-width: 100%;
  }
}